import { Component, OnInit } from '@angular/core';
import { Conquista } from 'src/app/models/conquista';
import { PacienteService } from 'src/app/services/paciente.service';
import { ConquistaService } from 'src/app/services/conquista.service';

@Component({
  selector: 'app-conquistas',
  templateUrl: './conquistas.page.html',
  styleUrls: ['./conquistas.page.scss'],
})
/** Tela responsável para o paciente acompanhar as conquistas alcançadas */
export class ConquistasPage implements OnInit {

  /** Lista de consquitas alcançadas */
  conquistas: Conquista[] = [];

  /** Lista de consquitas pendentes */
  conquistasNaoAlcandas: Conquista[] = [];

  constructor(private pacienteService:PacienteService, private conquistaService:ConquistaService) { }

  async ngOnInit() {
    let paciente = await this.pacienteService.getPacienteLogado();
    this.conquistas = await this.conquistaService.buscarConsquistasAlcancadas(paciente);
    this.conquistasNaoAlcandas = await this.conquistaService.buscarConsquistasPendentes(paciente);
  }

}
